import {
	syspointsetPages,
	//	syspointsetNopages,
	syspointsetAdd,
	syspointsetUpdate,
	syspointsetDelete,
	//	syspointsetDetail
} from 'api/api';
import storyform from './businessComponents/pointrulesForm';
let pageData = {
	ruleId: '',
	//	creator:'',
	//	language:'',
	//	modifyTime:'',
	//	modifyor:'',
	pointAmount: 100,
	pointCount: '',
	ruleEnUsTitle: '',
	ruleJaJpTitle: '',
	ruleKoKrTitle: '',
	ruleZhCnTitle: '',
	ruleTitle: '',
	status: 0, //1上架 0 下架
}
export default {
	data() {
		return {
			searchValue: '',
			tableData: {},
			dialogVisible: false,
			dialogTyle: '',

			page: {
				start: 0,
				length: 10,
				draw: 1,
			},
			currentPage: 1,
			pageSizes: [10, 20, 30],

			pageData: {
				...pageData
			},
		}
	},

	components: {
		storyform,
	},
	created() {
		this.init()
	},
	watch: {
		searchValue(value) {
			if(!value) {
				syspointsetPages(this.page).then(res => {
					this.tableData = res.data.obj
				})
			}
		}
	},
	methods: {
		init() {
			syspointsetPages(this.page).then(res => {
				this.tableData = res.data.obj
			})
		},
		onQuery() {
			if(this.searchValue) {
				syspointsetPages({ ...this.page,
					ruleTitle: this.searchValue
				}).then(res => {
					this.tableData = res.data.obj
				})
			}
		},
		showDilog(type, obj = pageData) {
			this.dialogVisible = true
			this.dialogTyle = type
			this.pageData = { ...obj
			}

		},
		onSubmit() {
			this.$refs.storyForm.validate(valid => valid && this.commitForm())
		},
		commitForm() {
			for(let key in this.pageData) {
				if(key.indexOf('Time') > -1) {
					delete this.pageData[key]
				}
			}
			if(this.dialogTyle === 'edit') {

				syspointsetUpdate(this.pageData).then(res => {
					this.init()
					this.dialogVisible = false
				})
			} else if(this.dialogTyle === 'add') {
				syspointsetAdd(this.pageData).then(res => {
					this.init()
					this.dialogVisible = false
				})
			}
		},
		handleDelete(obj) {
			this.$confirm(this.$t('commom.deleConfim'), '', {
				confirmButtonText: this.$t('commom.sure'),
				cancelButtonText: this.$t('commom.cancel'),
				type: 'warning'
			}).then(() => {

				syspointsetDelete({
					ruleId: obj.ruleId
				}).then(res => {
					this.init()
				})

			}).catch(res => {

			})

		},
		getpageData() {
			syspointsetPages({ ...this.page,
				ruleTitle: this.searchValue
			}).then(res => {
				this.tableData = res.data.obj
			})
		},
		handleSizeChange(val) {
			this.page.length = val
			this.page.start = (this.currentPage - 1) * this.page.length
			this.getpageData()
		},
		handleCurrentChange(val) {
			this.currentPage = val;
			this.page.start = (this.currentPage - 1) * this.page.length
			setTimeout(() => {
				this.getpageData()
			}, 0)
		},
	}
}



